import axios from 'axios'

export function findArea({ lat, long}) {
    return axios.post('/findArea/', {
        lat: lat,
        long: long
    })
}

export function getUserAreas({ lat, long}) {
    return axios.post('/getUserAreas/', {
        lat: lat,
        long: long
    })
}



